<template>
  <div class="home">
    <header>
      <div class="search">
        <img class="search_img1" src="../assets/images/icon_vip@2x.png" alt />
        <van-search @focus="onSearch()" />
        <img class="search_img2" src="../assets/images/icon_more1@2x.png" alt />
        <!-- <img class="search_img3" src="../assets/images/icon_search@2x.png" alt /> -->
        <div class="time">{{new Date().getDate()}}</div>
      </div>
      <div class="swiper">
        <van-swipe :autoplay="2000" indicator-color="#fff">
          <van-swipe-item v-for="(item,index) of headSwiper" :key="index">
            <img @click="onHeadSwiper()" :src="item" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
    </header>
    <nav>
      <div class="nav">
        <transition enter-active-class="animated fadeInDown">
          <div class="attention_box" v-show="foldFlag">
            <div class="attention" @click="onSwitchover()">
              <img
                :src="navAttention==true?require('../assets/images/icon_follow@2x.png'):require('../assets/images/icon_history@2x.png')"
                alt
              />
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInDown">
          <div class="nav_tab1" v-show="foldFlag">
            <div class="person-wrap" ref="personWrap">
              <ul class="person-list" ref="personTab">
                <li
                  ref="personItem"
                  v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                  :key="index"
                >
                  <div>
                    <img :src="item.img" alt />
                  </div>
                  <p>{{item.modulename}}</p>
                </li>
                <li ref="personItem" @click="onConcerns()">
                  <div>
                    <img src="../assets/images/icon_more@2x.png" alt />
                  </div>
                  <p>全部</p>
                </li>
              </ul>
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInUp">
          <div class="nav_tab" v-show="!foldFlag">
            <div class="title">
              <p @click="navAttention = true" :class="{nav_tab_active:navAttention==true}">关注</p>
              <p @click="navAttention = false" :class="{nav_tab_active:navAttention==false}">足迹</p>
            </div>
            <ul>
              <li
                ref="nav_tab"
                v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                :key="index"
              >
                <div>
                  <img :src="item.img" alt />
                </div>
                <p>{{item.modulename}}</p>
              </li>
              <li @click="onConcerns()">
                <div>
                  <img src="../assets/images/icon_more@2x.png" alt />
                </div>
                <p>全部</p>
              </li>
            </ul>
          </div>
        </transition>
      </div>
      <transition enter-active-class="animated fadeInUp">
        <div class="fold" v-show="foldShow">
          <div
            class="triangle"
            @click="foldFlag = !foldFlag,$refs.personItem.length <= 3?foldShow = false:foldShow = true"
          >
            <p>{{foldFlag==true?"展开":"收起"}}</p>
            <img
              :src="foldFlag==true?require('../assets/images/icon_retrac1@2x.png'):require('../assets/images/icon_retrac@2x.png')"
              alt
            />
          </div>
        </div>
      </transition>
    </nav>
    <section>
      <div class="sec_recommend">
        <div class="rec_content">
          <div class="rec_business">
            <div>
              <p>重点业务</p>
              <p>近期上线的N个视图</p>
            </div>
            <img src="../assets/images/icon_zdyw@2x.png" alt />
          </div>
          <div class="rec_feature">
            <p>分行特色</p>
            <img src="../assets/images/icon_fhts@2x.png" alt />
          </div>
          <div class="rec_incident">
            <p>事件</p>
            <img src="../assets/images/icon_event@2x.png" alt />
          </div>
        </div>
        <div class="rec_headline">
          <div class="logo">
            <img src="../assets/images/pic_zsbb@2x.png" alt />
          </div>
          <div class="swiper">
            <van-swipe style="height: 46px;" :autoplay="2000" vertical :show-indicators="false">
              <van-swipe-item v-for="(item,index) of secSwiper" :key="index">
                <p @click="onBroadcast(item)">
                  <span>{{item.broadcasttype}}</span>
                  {{item.broadcastname}}
                </p>
              </van-swipe-item>
            </van-swipe>
          </div>
        </div>
      </div>
      <div class="sec_tab">
        <van-sticky :offset-top="50" z-index="200">
          <img
            class="switch_style"
            :src="tabSection==true?require('../assets/images/icon_modular@2x.png'):require('../assets/images/icon_form@2x.png')"
            alt
            @click="tabSection=!tabSection"
          />
        </van-sticky>
        <van-tabs
          v-model="secTabActive"
          swipe-threshold="6"
          duration="0.6"
          sticky
          swipeable
          :offset-top="50"
          animated
          line-width="60"
          background="#f1f1f1"
        >
          <van-tab :title="item.modulegroupType" v-for="(item,index) of secTabContent" :key="index">
            <div class="tab_section" v-show="tabSection">
              <div class="tab_content" v-for="(item,index) of item.list" :key="index">
                <img @click="onSectionView(item)" class="img_top" :src="item.image" alt />
                <div class="title">
                  <img :src="item.icon" alt />
                  <p @click="onSectionView(item)">{{item.title}}</p>
                </div>
                <div class="bottom">
                  <div class="bo_l">
                    <img src="../assets/images/icon_visits@2x.png" alt />
                    <p>{{item.tel}}</p>
                    <img src="../assets/images/btn_good_normal@2x1.png" alt />
                    <p>{{item.sureCount}}</p>
                  </div>
                  <ul class="bo_r" @click="item.identifySub.shadeShow=true">
                    <li></li>
                    <li></li>
                    <li></li>
                  </ul>
                </div>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="shade" v-show="item.identifySub.shadeShow">
                    <div class="shade_con" @click="item.identifySub.shadeShow=false"></div>
                    <img
                      @click="onCollect(item)"
                      class="img1"
                      :src="item.identifySub.shadeImg==false?require('../assets/images/btn_collect_normal@2x.png'):require('../assets/images/btn_collect_selected@2x.png')"
                      alt
                    />
                    <img
                      @click="onLike(item)"
                      class="img2"
                      :src="item.identifySub.shadeImg1==false?require('../assets/images/btn_good_normal@2x.png'):require('../assets/images/btn_good_selected@2x.png')"
                      alt
                    />
                    <div class="disabled1" v-show="item.identifySub.shadeImg1"></div>
                    <img
                      @click="item.identifySub.shadeImg2 = true,item.identifySub.trampleShow = true,item.identifySub.shadeShow = false"
                      class="img3"
                      :src="item.identifySub.shadeImg2==false?require('../assets/images/btn_bad_normal@2x.png'):require('../assets/images/btn_bad_selected@2x.png')"
                      alt
                    />
                    <div class="disabled" v-show="item.identifySub.shadeImg2"></div>
                  </div>
                </transition>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="trample" v-show="item.identifySub.trampleShow">
                    <p @click="onTreadCause($event,item)">设计不佳</p>
                    <p @click="onTreadCause($event,item)">数据不准</p>
                    <p @click="onTreadCause($event,item)">不感兴趣</p>
                    <p
                      @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item"
                    >其他意见</p>
                  </div>
                </transition>
              </div>
            </div>
            <div class="tab_section1" v-show="!tabSection">
              <div class="tab_content" v-for="(item,index) of item.list" :key="index">
                <img @click="onSectionView(item)" :src="item.image" class="left" alt />
                <div class="right">
                  <div @click="onSectionView(item)" class="title">{{item.title}}</div>
                  <p @click="onSectionView(item)">{{item.text}}</p>
                  <div class="bottom">
                    <img src="../assets/images/icon_visits@2x.png" alt />
                    <div class="tel">{{item.tel}}</div>
                    <img src="../assets/images/btn_good_normal@2x1.png" alt />
                    <div class="tel">{{item.sureCount}}</div>
                    <ul class="bo_r" @click="item.identifySub.shadeShow=true">
                      <li></li>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </div>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="shade" v-show="item.identifySub.shadeShow">
                    <div class="shade_con" @click="item.identifySub.shadeShow=false"></div>
                    <img
                      @click="onCollect(item)"
                      class="img1"
                      :src="item.identifySub.shadeImg==false?require('../assets/images/btn_collect_normal@2x.png'):require('../assets/images/btn_collect_selected@2x.png')"
                      alt
                    />
                    <img
                      @click="onLike(item)"
                      class="img2"
                      :src="item.identifySub.shadeImg1==false?require('../assets/images/btn_good_normal@2x.png'):require('../assets/images/btn_good_selected@2x.png')"
                      alt
                    />
                    <div class="disabled1" v-show="item.identifySub.shadeImg1"></div>
                    <img
                      @click="item.identifySub.shadeImg2 = true,item.identifySub.trampleShow = true,item.identifySub.shadeShow = false"
                      class="img3"
                      :src="item.identifySub.shadeImg2==false?require('../assets/images/btn_bad_normal@2x.png'):require('../assets/images/btn_bad_selected@2x.png')"
                      alt
                    />
                    <div class="disabled" v-show="item.identifySub.shadeImg2"></div>
                  </div>
                </transition>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="trample" v-show="item.identifySub.trampleShow">
                    <p @click="onTreadCause($event,item)">设计不佳</p>
                    <p @click="onTreadCause($event,item)">数据不准</p>
                    <p @click="onTreadCause($event,item)">不感兴趣</p>
                    <p
                      @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item"
                    >其他意见</p>
                  </div>
                </transition>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </section>
    <van-overlay :show="writeShow" z-index="300">
      <div class="submitInfo" @click.stop>
        <van-field
          v-model="treadMsg"
          rows="6"
          autosize
          type="textarea"
          maxlength="100"
          placeholder="请输入具体意见描述"
          show-word-limit
        />
        <van-button
          type="primary"
          color="linear-gradient(270deg, #45A4B1 0%, #2A7875 100%)"
          block
          @click="onTreadIdea()"
        >提交</van-button>
        <img @click="writeShow = false,treadMsg = ''" src="../assets/images/btn_close@1x.png" alt />
      </div>
    </van-overlay>
  </div>
</template>
<script>
import data from "../assets/js/data.js";
import BScroll from "better-scroll";
export default {
  name: "Home",
  components: {
    Tabbar: () => import("../components/Tabbar")
  },
  data() {
    return {
      navAttention: true,
      foldFlag: true,
      foldShow: true,
      tabSection: true,
      secTabActive: 0,
      secConObj: {},
      writeShow: false,
      treadMsg: "",
      navAttentionArr: [],
      navFootprintArr: [],
      headSwiper: [
        require("../assets/images/1.png"),
        require("../assets/images/2.png")
      ],
      secSwiper: [],
      secTabContent: []
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBroadcast(); //掌上播报
      this.getSectionView(); //section数据
      this.getNavAttentionData(); //nav关注
      this.getNavFootprintData(); //nav足迹
      this.getHeadSwiper(); //header轮播
    });
  },
  methods: {
    //nav滚动条
    personScroll() {
      this.$nextTick(() => {
        let width = (this.$refs.personItem.length + 1) * 80;
        this.$refs.personTab.style.width = width + "px";
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.personWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            // 忽略竖直方向的滚动
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    //点击搜索栏
    onSearch() {
      this.$router.push({ path: "/search" });
    },
    //获取header轮播数据
    getHeadSwiper() {
      this.$axios
        .post("/hope3/hopeImagebar/queryHopeImagebar", {
          aamid: "010203",
          deptid: 5
        })
        .then(res => {
          // console.log(res);
        });
    },
    //点击header轮播
    onHeadSwiper() {
      this.$axios
        .post("/hope3/hopeImagebar/queryModuleUrl", {
          imagebarid: "010203"
        })
        .then(res => {
          // console.log(res);
        });
    },
    //获取nav关注数据
    getNavAttentionData() {
      this.$axios
        .post("/hope3/hopeModule/queryWatchHopeModule", {
          aamid: "010203"
        })
        .then(res => {
          // console.log(res);
          if (res.status === 200 && res.data.status === "000000") {
            this.navAttentionArr = [...res.data.data] || [];
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                this.$refs.personItem.length <= 3
                  ? (this.foldShow = false)
                  : (this.foldShow = true);
              }
            }, 0);
          }
        });
    },
    //获取nav足迹数据
    getNavFootprintData() {
      this.$axios
        .post("/hope3/hopeModule/queryMyFoot", {
          aamid: "1",
          status: "1"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.navFootprintArr = [...res.data.data] || [];
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                this.$refs.personItem.length <= 3
                  ? (this.foldShow = false)
                  : (this.foldShow = true);
              }
            }, 0);
          }
        });
    },
    //点击切换nav关注和足迹
    onSwitchover() {
      this.navAttention = !this.navAttention;
      let timer = setTimeout(() => {
        if (timer) {
          clearTimeout(timer);
          this.personScroll();
          this.$refs.personItem.length <= 3
            ? (this.foldShow = false)
            : (this.foldShow = true);
        }
      }, 0);
    },
    //nav关注更多
    onConcerns() {
      if (this.navAttention == true) this.$router.push({ path: "/myConcerns" });
    },
    //点击掌上播报
    onBroadcast(item) {
      this.$router.push({
        path: "/broadcast",
        query: { name: item.broadcasttype }
      });
    },
    //获取掌上播报数据
    getBroadcast() {
      this.$axios
        .post("/hope3/hopeBroadcast/queryHopeBroadcast", {
          aamid: "001151515",
          deptid: "0010101032"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secSwiper = [...res.data.data] || [];
          }
        });
    },
    //section收藏和取消收藏
    onCollect(item) {
      item.identifySub.shadeImg = !item.identifySub.shadeImg;
      if (item.identifySub.shadeImg == false) {
        this.$axios
          .post("/hope3/hopeModule/deleteWatchHopeModule", {
            aamid: "010203",
            moduleid: item.moduleid
          })
          .then(res => {
            console.log(res);
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
              this.$toast({
                message: "取消收藏",
                icon: require("../assets/images/btn_collect_normal@2x.png")
              });
            }
          });
      } else {
        this.$axios
          .post("/hope3/hopeModule/insertWatchHopeModule", {
            aamid: "010203",
            moduleid: item.moduleid
          })
          .then(res => {
            console.log(res);
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
              this.$toast({
                message: "已收藏",
                icon: require("../assets/images/btn_collect_selected@2x.png")
              });
            }
          });
      }
    },
    //section点赞
    onLike(item) {
      this.$axios
        .post("/hope3/hopeComments/insertHopeComments", {
          aamid: "010203",
          moduleid: item.moduleid,
          logtime: Date.parse(new Date()),
          comments: 1,
          description: ""
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.shadeImg1 = true;
            this.getSectionView();
            console.log(res);
          }
        });
    },
    //section点踩原因
    onTreadCause(e, item) {
      this.$axios
        .post("/hope3/hopeComments/insertHopeComments", {
          aamid: "010203",
          moduleid: item.moduleid,
          logtime: Date.parse(new Date()),
          comments: 0,
          description: e.target.innerHTML
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.trampleShow = false;
            console.log(res);
          }
        });
    },
    //section点踩意见
    onTreadIdea() {
      this.$axios
        .post("/hope3/hopeComments/insertHopeComments", {
          aamid: "010203",
          moduleid: this.secConObj.moduleid,
          logtime: Date.parse(new Date()),
          comments: 0,
          description: this.treadMsg
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.writeShow = false;
            this.treadMsg = "";
            console.log(res);
          }
        });
    },
    //点击section跳转增加足迹
    onSectionView(item) {
      this.$axios
        .post("/hope3/hopeUserLog_h/insertUserLog_h", {
          aamid: "1",
          moduleid: item.moduleid,
          logtime: Date.parse(new Date())
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.getSectionView();
            this.getNavFootprintData();
            // console.log(res);
          }
        });
    },
    //获取section数据
    getSectionView() {
      this.$axios
        .post("/hope3/hopeModule/searchMoudleByGroup", {
          aamid: "010203",
          deptid: "111213",
          odeptid: "0010101032"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secTabContent = [...res.data.data] || [];
          }
        });
    }

    // go(){
    //   if(workNum==='001017909'||workNum==='888803996'||workNum==='001174901'){
    //     window.location.replace('http://76.7.66.5:8080/mobile/aggPay/index')
    //   }else{
    //     window.location.replace('http://76.7.66.5:8080/mobile')
    //   }
    // }
    // getUserInfo() {
    //   window["rh"] && window["rh"].getUserInfo({}, function(result) {
    //       alert(result);
    //     }, function(err) {
    //       alert(err);
    //     }
    //   );
    // },
  }
};
</script>
<style lang="less" scoped>
.animated {
  animation-duration: 0.5s;
  animation-fill-mode: both;
}
.home {
  min-height: 100%;
  overflow: hidden;
  background: #f1f1f1;
  margin-bottom: 0.49rem;
  .submitInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    padding: 1rem 0.2rem;
    box-sizing: border-box;
  }
  header {
    width: 100%;
    background-image: linear-gradient(0deg, #f1f1f1 0%, #ffffff 84%);
    .search {
      width: 100%;
      height: 0.5rem;
      background: #ffffff;
      display: flex;
      justify-content: space-between;
      padding: 0 0.1rem;
      box-sizing: border-box;
      position: relative;
      position: fixed;
      left: 0;
      top: 0;
      z-index: 15;
      .van-search {
        margin-bottom: 1rem;
        border-radius: 0.15rem;
        width: 2.77rem;
        height: 0.3rem;
        border: 0.015rem solid #45a4b1;
        padding: 0;
        margin-top: 0.1rem;
        .van-search__content {
          height: 100%;
          border-radius: 0.15rem;
          .van-cell {
            padding: 0;
            /deep/ .van-icon {
              margin-top: 0.02rem;
            }
            /deep/ .van-field__body {
              margin-top: 0.02rem;
            }
          }
        }
      }
      .search_img1 {
        width: 0.22rem;
        height: 0.22rem;
        margin-top: 0.14rem;
      }
      .search_img2 {
        width: 0.22rem;
        height: 0.22rem;
        margin-top: 0.14rem;
      }
      // .search_img3 {
      //   width: 0.2rem;
      //   height: 0.2rem;
      //   position: absolute;
      //   left: 0.62rem;
      //   top: 0.17rem;
      // }
      .time {
        width: 0.22rem;
        height: 0.22rem;
        position: absolute;
        left: 0.097rem;
        top: 0.145rem;
        font-size: 0.1rem;
        color: #232323;
        text-align: center;
        line-height: 0.18rem;
        padding-top: 0.04rem;
        box-sizing: border-box;
        z-index: 10;
      }
    }
    .swiper {
      width: 95%;
      height: 1.17rem;
      background: #000;
      bottom: -0.17rem;
      border-radius: 0.05rem;
      margin: auto;
      margin-top: 0.5rem;
      /deep/ .van-swipe {
        width: 100%;
        height: 100%;
        border-radius: 0.05rem;
        transform: translateZ(0);
        img {
          width: 100%;
          height: 100%;
          border-radius: 0.05rem;
        }
      }
      /deep/ .van-swipe__indicator {
        background-color: #fff;
        width: 0.07rem;
        height: 0.01rem;
        border-radius: 0;
      }
    }
  }
  nav {
    width: 100%;
    margin-top: 0.12rem;
    .nav {
      display: flex;
      width: 100%;
    }
    .attention_box {
      width: 0.79rem;
      margin-left: -0.45rem;
      overflow: hidden;
      .attention {
        width: 100%;
        height: 0.79rem;
